<template>
  <div>
    <NavTemplent title="部门负责人指标库"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="tableTitle">
        <span>
          <div>人事部门</div>
        </span>
      </div>
      <div>
        <div id="myChart" style="width: 100%;height: 300px;padding: 15px"></div>
      </div>
      <div class="tableTitle">
        <span>
          <div>业务部门</div>
        </span>
      </div>
      <div>
        <div id="myChart1" style="width: 100%;height: 300px;padding: 15px"></div>
      </div>
      </div>
    </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                title: '编辑费用',
                form: {
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    departmentId: '',
                    siteName: ''
                }
            }
        },
        methods: {
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    title: {
                        text: '折线图堆叠'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['到面数','约面数','入职数','离职数','指标完成数']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['2019/01','2019/02','2019/03','2019/04','2019/05','2019/06','2019/07']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            name:'到面数',
                            type:'line',
                            stack: '总量',
                            data:[120, 132, 101, 134, 90, 230, 210]
                        },
                        {
                            name:'约面数',
                            type:'line',
                            stack: '总量',
                            data:[220, 182, 191, 234, 290, 330, 310]
                        },
                        {
                            name:'入职数',
                            type:'line',
                            stack: '总量',
                            data:[150, 232, 201, 154, 190, 330, 410]
                        },
                        {
                            name:'离职数',
                            type:'line',
                            stack: '总量',
                            data:[320, 332, 301, 334, 390, 330, 320]
                        },
                        {
                            name:'指标完成数',
                            type:'line',
                            stack: '总量',
                            data:[820, 932, 901, 934, 1290, 1330, 1320]
                        }
                    ]
                });
            },
            drawLine1() {
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                myChart.setOption({
                    title: {
                        text: '折线图堆叠'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['营业额','销售量','销售单价','聊天人数','指标完成数']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['2019/01','2019/02','2019/03','2019/04','2019/05','2019/06','2019/07']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            name:'营业额',
                            type:'line',
                            stack: '总量',
                            data:[120, 132, 101, 134, 90, 230, 210]
                        },
                        {
                            name:'销售量',
                            type:'line',
                            stack: '总量',
                            data:[220, 182, 191, 234, 290, 330, 310]
                        },
                        {
                            name:'销售单价',
                            type:'line',
                            stack: '总量',
                            data:[150, 232, 201, 154, 190, 330, 410]
                        },
                        {
                            name:'聊天人数',
                            type:'line',
                            stack: '总量',
                            data:[320, 332, 301, 334, 390, 330, 320]
                        },
                        {
                            name:'指标完成数',
                            type:'line',
                            stack: '总量',
                            data:[820, 932, 901, 934, 1290, 1330, 1320]
                        }
                    ]
                });
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            }
        },
        mounted() {
            this.getDepartmentList();
            this.drawLine();
            this.drawLine1();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



